import React from "react";

export default function Documents({ props }) {
  return (
    <fieldset>
      <div className="row mt-2">
        <div className="col-lg-12">
          <div className="form-group">
            <label>
              Restaurant Name <strong className="text-danger">*</strong>
            </label>
            <div className="custom-file">
              <input id="logo" type="file" className="custom-file-input" />
              <label htmlFor="logo" className="custom-file-label">
                Restaurant Image
              </label>
            </div>
          </div>
        </div>

        <div className="col-lg-12 mt-2">
          <div className="form-group">
            <label>
              Banner Image <strong className="text-danger">*</strong>
            </label>
            <div className="custom-file">
              <input id="logo" type="file" className="custom-file-input" />
              <label htmlFor="logo" className="custom-file-label">
                Banner Image
              </label>
            </div>
          </div>
        </div>

        <div className="col-lg-5 mt-2">
          <div className="form-group">
            <label>Image Name</label>
            <input type="text" className="form-control" />
          </div>
        </div>
        <div className="col-lg-5 mt-2">
          <div className="form-group">
            <label>Image</label>
            <div className="custom-file">
              <input id="logo" type="file" className="custom-file-input" />
              <label htmlFor="logo" className="custom-file-label">
                Choose File...
              </label>
            </div>
          </div>
        </div>

        <div className="form-inline mt-3">
          <button className="btn btn-danger mr-2">
            <i className="fa fa-trash-o" />
          </button>
          <button className="btn btn-primary">
            <i className="fa fa-plus" />
          </button>
        </div>

        <div className="col-lg-12 justify-content-end">
          <button className="btn btn-primary float-right mr-2 ">Next</button>
          <button className="btn btn-default float-right mr-2 ">
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
