import React from "react";
import DynamicMeal from "./DynamicMeal";

export default function Meals() {
  return (
    <div className="tabs-container mt-1">
      <ul className="nav nav-tabs" role="tablist">
        <li>
          <a className="nav-link active" data-toggle="tab" href="#tab-1">
            {" "}
            Monday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Tuesday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Wednesday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Thursday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Friday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Saturday
          </a>
        </li>
        <li>
          <a className="nav-link" data-toggle="tab" href="#tab-2">
            Sunday
          </a>
        </li>
      </ul>
      <div className="tab-content">
        <div role="tabpanel" id="tab-1" className="tab-pane active">
          <div className="panel-body">
            <DynamicMeal index={1} />
          </div>
        </div>
        <div role="tabpanel" id="tab-2" className="tab-pane">
          <div className="panel-body">
            <DynamicMeal index={2} />
          </div>
        </div>
      </div>
    </div>
  );
}
