import axios from "axios";
import React, { useState, useEffect, useReducer } from "react";
import { BASIC_INFO, restaurant } from "../../../actions/actions";
import RestaurantReducer from "../../../reducers/reducers";

export default function BasicInfo(props) {
  const [cuisine, setCuisine] = useState([]);
  const [user, setUser] = useReducer(RestaurantReducer, restaurant);
  const onChangeText = ({ target }) => {
    setUser({
      type: BASIC_INFO,
      payload: { [target.name]: target.value},
    });
  };
  const handleContinue = (e) => {
    e.preventDefault();
    console.log(user);
    props.goToStep(2)
  };

  useEffect(() => {
    axios
      .get("http://munkybox-admin.herokuapp.com/api/cuisine")
      .then((res) => setCuisine(res.data))
      .catch((err) => {
        console.log(err);
      });
  }, [cuisine]);

  return (
    <fieldset>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              RESTAURANT NAME <strong className="text-danger">*</strong>
            </label>
            <input
              name="restaurant_name"
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              OWNER NAME <strong className="text-danger">*</strong>
            </label>
            <input
              name="owner_name"
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              STATE <strong className="text-danger">*</strong>
            </label>
            <input
              name="state"
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              CUISINE <strong className="text-danger">*</strong>
            </label>
            <select
              name="cuisine_type"
              onChange={(e) => onChangeText(e)}
              className="form-control required"
            >
              {cuisine.map((data, key) => (
                <option
                  name={data.cuisinename}
                  value={data.cuisineName}
                  key={key}
                >
                  {data.cuisineName}
                </option>
              ))}
            </select>
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              PHONE NUMBER <strong className="text-danger">*</strong>
            </label>
            <input
              name="phone"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              STREET <strong className="text-danger">*</strong>
            </label>
            <input
              name="locality"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              COUNTRY <strong className="text-danger">*</strong>
            </label>
            <input
              name="country"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              COMMISSION <strong className="text-danger">*</strong>
            </label>
            <input
              name="commission"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
        </div>

        <div className="col-lg-4">
          <div className="form-group">
            <label>
              EMAIL ADDRESS <strong className="text-danger">*</strong>
            </label>
            <input
              name="email"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              CITY <strong className="text-danger">*</strong>
            </label>
            <input
              name="city"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              POSTAL CODE <strong className="text-danger">*</strong>
            </label>
            <input
              name="postal_code"
              
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              STATUS <strong className="text-danger">*</strong>
            </label>
            <select className="form-control required">
              <option name="active" value="active">
                Active
              </option>
              <option name="inactive" value="inactive">
                Inactive
              </option>
            </select>
          </div>
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Meal Time <strong className="text-danger">*</strong>
            </label>
            <select className="form-control required">
              <option name="active" value="active">
                Lunch
              </option>
              <option name="inactive" value="inactive">
                Dinner
              </option>
            </select>
          </div>
        </div>
        <div className="col-lg-8">
          <div className="form-group">
            <label>
              About <strong className="text-danger">*</strong>
            </label>
            <textarea className="form-control form-control-plaintext" />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            className="btn btn-primary float-right mr-2"
            onClick={handleContinue}
          >
            Next
          </button>
        </div>
      </div>
    </fieldset>
  );
}
