import React from 'react'

export default function DynamicMeal({dynamicMeal,index}) {
    return (
      <fieldset key={index}>
        <legend>Meal 1</legend>
        <div className="row mt-2">
          <div className="col-lg-4">
            <div className="form-group">
              <label>
                AVAILABLE AS <strong className="text-danger">*</strong>
              </label>
              <select
                id="name"
                name="name"
                type="text"
                className="form-control required"
              >
                <option name="Lunch" value="Lunch">
                  Lunch
                </option>
                <option name="Dinner" value="Dinner">
                  Dinner
                </option>
              </select>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="form-group">
              <label>
                MEAL TYPE <strong className="text-danger">*</strong>
              </label>
              <select
                id="surname"
                name="surname"
                type="text"
                className="form-control required"
              >
                <option name="veg" value="veg">
                  Veg
                </option>
                <option name="non-veg" value="non-veg">
                  Non-Veg
                </option>
              </select>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="form-group">
              <label>
                DAY <strong className="text-danger">*</strong>
              </label>
              <select
                id="surname"
                name="surname"
                type="text"
                className="form-control required"
              >
                <option name="Monday" value="Monday">
                  Monday
                </option>
                <option name="Tuesday" value="Tuesday">
                  Tuesday
                </option>
                <option name="Wednesday" value="Wednesday">
                  Wednesday
                </option>
                <option name="Thursday" value="Thursday">
                  Thursday
                </option>
                <option name="Friday" value="Friday">
                  Friday
                </option>
                <option name="Saturday" value="Saturday">
                  Saturday
                </option>
                <option name="Sunday" value="Sunday">
                  Sunday
                </option>
              </select>
            </div>
          </div>
        </div>
        <div className="row mt-2">
          <div className="col-lg-4">
            <div className="form-group">
              <label>
                Meal Name <strong className="text-danger">*</strong>
              </label>
              <input
                id="email"
                name="email"
                type="text"
                className="form-control required email"
              />
            </div>
          </div>
          <div className="col-lg-8">
            <div className="form-group">
              <label>Meal Image</label>
              <div className="custom-file">
                <input id="logo" type="file" className="custom-file-input" />
                <label htmlFor="logo" className="custom-file-label">
                  Choose File...
                </label>
              </div>
            </div>
          </div>
        </div>
        <div className="row mt-2">
          <div className="col-lg-12">
            <div className="form-group">
              <label>
                Meal Description <strong className="text-danger">*</strong>
              </label>
              <textarea className="form-control form-control-plaintext" />
            </div>
          </div>
        </div>
        <div className="row mt-2">
          <div className="col-lg-3">
            <div className="form-group">
              <label>Add On</label>
              <input type="text" className="form-control" />
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label>Price</label>
              <input type="text" className="form-control" />
            </div>
          </div>

          <div className="col-lg-4">
            <div className="form-group">
              <label>Add On Image</label>
              <div className="custom-file">
                <input id="logo" type="file" className="custom-file-input" />
                <label htmlFor="logo" className="custom-file-label">
                  Choose File...
                </label>
              </div>
            </div>
          </div>
          <div className="col-lg-2">
            <div className="form-inline mt-4 py-2">
              <button className="btn btn-default mr-2">
                <i className="fa fa-plus" />
              </button>
              <button className="btn btn-danger">
                <i className="fa fa-trash-o" />
              </button>
            </div>
          </div>
        </div>
      </fieldset>
    );
}
