import { BASIC_INFO, meal, SET_MEALS } from "../actions/actions";
import { restaurant } from "../actions/actions";
const initialState = {
  restaurant: restaurant,
  meals: meal,
};
export default function RestaurantReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case BASIC_INFO:
      return { ...state, ...payload };
    case SET_MEALS:
      console.log('Meal added');
      return { ...state, payload };
    default:
      break;
  }
}
